.main{
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin: 20px 10px;
}
.head_cube{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: calc(100% - 20px);
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid var(--boxColor);
  border-radius: 8px;
}
.head_box{
  display: flex;
  justify-content: flex-start;
}
.head_box > div{
  width: 33%;
}

.content_box{
  display: flex;
}

.content_box > div{
  display: flex;
  padding: 10px 5px 15px 0;
  font-weight: 600;
  font-size: 16px;
}

.select{
  height: 30px;
  margin-right: 10%;
  color: var(--boxColor);
}

.night,.sun{
  position: absolute;
  top: 5px;
  right: 5px;
  background-color: var(--boxColor);
  padding: 5px;
  border-radius: 20px;
  transition: all ease-out 0.5s;
}
.night{
  transform: rotate(0);
}
.sun{
  transform: rotate(90deg);
}

.unique_title{
  font-size: 20px;
  font-weight: 600;
  width: 80px;
}

.warning{
  color:  #D12E3f;
  font-size: 18px;
  font-weight: 500;
  background-color: #F0E4B3;
  border: 1px solid black;
  border-radius: 8px;
  padding: 10px;
}

.warning > div{
  border-radius: 8px;
  padding: 2px;
}

.explanation{
  position: absolute;
  right: 30px;
  top: 90px;
  display: flex;
  width: calc(80% - 180px) ;
  padding: 10px;
  justify-content: center;
  align-items: center;
  color: #3D2C1D;
  font-size: 18px;
  font-weight: 500;
  background-color: #FADF7F;
  border: 2px solid #050502;
  border-radius: 5px;
  box-shadow: inset 0px 0px 5px 5px #FFDC6B;
}

.container{
  display: grid;
  grid-template-columns: repeat(2, calc(50% - 10px));
  background-color: #FADF7F;
  grid-gap: 10px;
  padding: 10px;
  border-radius: 8px;
}
.container > div{
  height:70%;
  padding: 5px;
  margin: 10px;
  border-radius: 2px;
  background-color:#F0E4B3;
  border-radius: 5px;
  border: 4px solid #050502;
  color: #3D2C1D;
  font-weight: 600;
  font-size: 18px;
}
.training_block{
  height: 90%;
}
.training_title {
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 5px;
}

.stat_box {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0 0 25px 10px;
}

.stat_row {
  margin-bottom: 5px;
}