:root[color-theme='light'] {
  --background: #DAD6D2;
  --boxColor: #2D2925;
  --wrringColor: #D12E3F;
}

:root[color-theme='dark'] {
  --background: rgba(22, 20, 18, 0.80);;
  --boxColor: #F6F5F4;
  --wrringColor: #F95263;
}

html {
  background: var(--background);
  color: var(--boxColor);
  transition: all ease-out 0.5s;
}


input{
  border: none;
  margin-bottom: 10px;
  max-width: 30%;
  color: var(--boxColor);
  border-bottom: 1px solid var(--boxColor);
  background-color: transparent;
  transition: all ease-out 0.3s;
}

input:focus {
  outline: none;
}

select{
  border: 1px solid var(--boxColor);
  background-color: transparent;
  transition: all ease-out 0.3s;
  border-radius: 12px;
  padding: 3px;
}
